// var utils = require("./utils");
const path = require('path')
var AsyncFs = require('../utils/async-fs')
var xtpl = require('xtpl')
// If your plugin is direct dependent to the html webpack plugin:

class WebpackTemplatePlugin {
  // 
  constructor(options) {
    console.log('WebpackTemplatePlugin=======', options)
    if (options && options.config) {
      let config = options.config
      if (config.plugins && config.plugins.length > 0) {
        if (options.tpl && options.tpl.length > 0) {
          options.tpl.forEach(item => {
            if (!item.renderObject) {
              item.renderObject = {
                plugins: config.plugins
              }
            }
          })
        }
      }
    }
    this.options = options
    // console.log(options);
  }
  // 
  apply(compiler) {
    // 
    let options = this.options
    if (compiler.hooks) {
      compiler.hooks.entryOption.tap(
        'WebpackTemplatePlugin',
        (context, entry) => {
          console.log('================WebpackTemplatePlugin==================')
          if (options.tpl && options.tpl.length > 0) {
            options.tpl.forEach(async item => {
              try {
                await new Promise((resolve, reject) => {
                  xtpl.renderFile(
                    item.src,
                    {
                      renderObject: item.renderObject
                    },
                    async function(error, content) {
                      if (error) {
                        reject(error)
                      }
                      console.log(content)
                      await AsyncFs.writeFile(item.dest, content)
                        .then(result => {
                          console.log('writeFile success')
                          resolve()
                        })
                        .catch(err => {
                          console.log(err)
                          reject(err)
                        })
                    }
                  )
                })
              } catch (error) {
                throw new Error(
                  'WebpackTemplatePlugin:  ' + error
                )
              }
            })
          }
          console.log('WebpackTemplatePlugin end')
        }
      )
    } else {
      compiler.plugin('entryOption', function(context, entry) {
        console.log('================WebpackTemplatePlugin================')
        if (options.tpl && options.tpl.length > 0) {
          options.tpl.forEach(async item => {
            try {
              await new Promise((resolve, reject) => {
                xtpl.renderFile(
                  item.src,
                  {
                    renderObject: item.renderObject
                  },
                  async function(error, content) {
                    if (error) {
                      reject(error)
                    }
                    console.log(content)
                    await AsyncFs.writeFile(item.dest, content)
                      .then(result => {
                        console.log('writeFile success')
                        resolve()
                      })
                      .catch(err => {
                        console.log(err)
                        reject(err)
                      })
                  }
                )
              })
            } catch (error) {
              throw new Error('WebpackTemplatePlugin:  ' + error)
            }
          })
        }
      })
    }
  }
}
// module.exports = WebpackTemplatePlugin
// export default WebpackTemplatePlugin
export { WebpackTemplatePlugin }
